export default {
	data() {
		return {
			user:{},
		};
	},
	onLoad() {
		
		
	},
	onShow: function() {
		
		console.log(this.commonData);
		uni.login({
		  provider: 'weixin',
		  success:  (loginRes)=> {
		    console.log("login:",loginRes);
		    // 获取用户信息
		    uni.getUserInfo({
		      provider: 'weixin',
			  withCredentials:true,
		      success:  (infoRes)=> {
				  // #给 uesr 赋值
		        console.log('用户：', infoRes);
				this.user = this.commonData.data.user = infoRes.userInfo;
		      }
		    });
		  }
		});
		console.log(this.user);

	},
	methods: {
		changeSkin(){
			uni.navigateTo({
					url: 'userInfo/userInfo?user='+ encodeURIComponent(JSON.stringify(this.user))
			});
		},
		
		login(){

			uni.showModal({
			    title: '是否跳转登录界面？',
			    success: function (res) {
			        if (res.confirm) {
			           uni.navigateTo({
			           		url: '../index/login'
			           });
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		}

	}
}
